<template>
    <div class="Nav">
        <ul class="nav-list">
            <router-link class="list-item" :class="{active : active == 'home'}" tag="li" to="home">
                <img src="../assets/img/shouye.png" alt="">
                <p>首页</p>
            </router-link>
            <router-link class="list-item" :class="{active : active == 'getSmartCarData'}" tag="li" to="getSmartCarData">
                <img src="../assets/img/celiangtongji.png" alt="">
                <p>测量数据</p>
            </router-link>
            <!-- Father 检测项目 -->
            <router-link class="list-item father" :class="{active : active == 'clProject'}" tag="li" to="clProject">
                <img src="../assets/img/celiangbiaozhun.png" alt="">
                <p>检测项目</p>
                <!-- <img class="right" src="../assets/img/right.png" alt=""> -->
            </router-link>
            <!-- <router-link class="list-item child-empty" tag="li" to="/home">
                <p>检测结果</p>
            </router-link> -->
            <!-- Father 检测项目 -->
            <router-link class="list-item father" :class="{active : active == 'surveyArchives'}"  tag="li" to="/surveyArchives">
                <img src="../assets/img/celiangdangan.png" alt="">
                <p>测量档案</p>
                <!-- <img class="right" src="../assets/img/right.png" alt=""> -->
            </router-link>
            <!-- <router-link class="list-item child-empty" tag="li" to="/home">
                <p>检测结果</p>
            </router-link> -->
            
            <router-link class="list-item" tag="li"  :class="{active : active == 'geTqualitystandard'}" to="geTqualitystandard">
                <img src="../assets/img/xitongguanli.png" alt="">
                <p>质量分析</p>
            </router-link>


            <!-- Father 检测项目 -->
            <li class="list-item father system" @click="changeShowNav">
                <img src="../assets/img/celiangxiangmu.png" alt="">
                <p>系统管理</p>
                <img class="right" :class="showNav ? 'active' : ''" src="../assets/img/right.png" alt="">
            </li>
            <div v-show="showNav">
                <router-link class="list-item child-father" :class="{active : active == 'measureStandard'}" tag="li" to="measureStandard">
                    <p>测量标准</p>
                    <!-- <img class="right" src="../assets/img/right.png" alt=""> -->
                </router-link>
                <!-- <router-link class="list-item child-empty-2" :class="{active : active == 'measureStandardTemplate'}" tag="li" to="measureStandardTemplate">
                    <p>测量标准模板</p>
                </router-link> -->
                <!-- <router-link class="list-item child-empty-3" :class="{active : active == 'gaugeMeasureStandard'}" tag="li" to="gaugeMeasureStandard">
                    <p>轨距测量标准</p>
                </router-link> -->
                <router-link class="list-item child-empty" :class="{active : active == 'typeMange'}" tag="li" to="/typeMange">
                    <p>道岔类型管理</p>
                </router-link>
                <router-link class="list-item child-empty"  :class="{active : active == 'line'}" tag="li" to="/line">
                    <p>线路</p>
                </router-link>
                <router-link class="list-item child-empty" :class="{active : active == 'site'}" tag="li" to="/site">
                    <p>站点</p>
                </router-link>
                <router-link class="list-item child-empty" :class="{active : active == 'siteSetting'}" tag="li" to="/siteSetting">
                    <p>站点设置</p>
                </router-link>
                <router-link class="list-item child-empty" :class="{active : active == 'peopleManage'}"  tag="li" to="/peopleManage">
                    <p>人员管理</p>
                </router-link>
                <router-link class="list-item child-empty" :class="{active : active == 'systemLog'}" tag="li" to="/systemLog">
                    <p>系统日志</p>
                </router-link>
                <router-link class="list-item child-empty" :class="{active : active == 'tqualitystandard'}"  tag="li" to="/tqualitystandard">
                    <p>质量分析标准</p>
                </router-link>
            </div>
        </ul>
    </div>
</template>
<script>
export default {
    name: 'my-nav',
    data() {
        return {
            active: '',
            showNav: false
        }
    },
    methods: {
        changeShowNav() {
            this.showNav = !this.showNav;
        }
    },
    watch:{
        $route(to){
            console.log(to.path);
            this.active = to.path.replace(/\//, '');
        }
    },
    created() {
      console.log(this.$route.path)
      this.active = this.$route.path.replace(/\//, '');
    },
}
</script>
<style lang="stylus" scoped>
.Nav
    .nav-list
        color #fff
        .list-item
            display flex
            align-items center
            cursor pointer
            padding 4px 10px
            border-radius 10px
            line-height 35px
            height 35px
            box-sizing border-box
            p
                padding-left 10px
        .active
            background #38f
        .father
            position relative
            .right
                position absolute
                top 50%
                right 0
                transform translateY(-50%)
                width 20px
                height 20px
            .active 
                transform translateY(-50%) rotate(90deg)
                background none
        .child-empty
            padding-left 34px
        .child-father
            position relative
            padding-left 34px
            .right
                position absolute
                top 50%
                right 0
                transform translateY(-50%)
                width 20px
                height 20px
        .child-empty-2
            padding-left 50px
        .child-empty-3
            padding-left 70px

</style>


