<template>
  <div class="home">
    <!-- Header -->
    <el-row class="Header">
      <el-col :span="24">
        <div class="grid-content bg-purple-dark">
          <div class="select-item">
            <!-- 线路 -->
            <p>路线：</p>
            <el-dropdown class="select_" @command="changeLine">
              <span class="el-dropdown-link">
                {{save.line.routename}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-for="item in line" :key="item.id" :command="item">{{item.routename}}</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <!-- 站点 -->
            <p style="margin-left: 20px;">站点：</p>
            <el-dropdown class="select_"  @command="changeSite">
              <span class="el-dropdown-link">
                {{save.site.sitename}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-for="item in site" :key="item.id" :command="item">{{item.sitename}}</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
      </el-col>
    </el-row>
    <!-- Header END -->
    
    <el-row>
      <!-- 表格 -->
      <el-col :span="14">
        <div class="grid-content bg-purple">
          <div class="tableCon">
                <p class="title">检测结果</p>
                  <el-table :data="tableData" style="width: 100%;border: 1px solid #eee;border-bottom: none;">
                  <el-table-column prop="ausername" label="项目名称">
                  </el-table-column>
                  <el-table-column prop="ptime" label="检测时间">
                  </el-table-column>
                  <el-table-column prop="warningstatistics" label="预警统计">
                  </el-table-column>
              </el-table>

            <div class="fy">
              <el-pagination
                  background
                  layout="prev, pager, next"
                  :total="tableData.length">
              </el-pagination>
            </div>
          </div>
        </div>
      </el-col>
      <!-- 天气 -->
      <el-col :span="10">
        <div class="grid-content bg-purple-light">
          <div class="wratter">
            <div class="con">
                <el-row>
                  <el-col :span="10">
                    <div class="grid-content bg-purple">
                        <div class="weather"  v-show="rStatus">
                          <!-- <img style="width: 60px; margin-left: 20px;" src="@/assets/img/weather/暴雨.png" alt=""> -->
                          <p>温度：{{save.weather.temp}}℃</p>
                          <p>湿度：{{save.weather.SD}}</p>
                          <p>气压：{{save.weather.AP}}</p>
                          <p>{{save.weather.WD}}：{{save.weather.WS}}</p>
                          <p>城市：{{save.weather.city}}</p>
                        </div>
                        <div class="tt"  v-show="!rStatus && rrStatus">
                          <i class="el-icon-warning" style="font-size: 55px;color: #409EFF"></i>
                          <p class="tips">此站点没有相关信息</p>
                        </div>
                    </div>
                  </el-col>
                  <el-col :span="14">
                    <div class="grid-content bg-purple-light">
                      <div class="text-con" v-show="rrStatus">
                        <p class="p1"> 项目概况： {{save.projectMsg.projectoverview || '未填写'}}</p>
                        <p class="p2"> 业主通知： {{save.projectMsg.noticeowner || '未填写'}}</p>
                        <p class="p3"> 地理位置： {{save.projectMsg.geographical || '未填写'}}</p>
                        <p class="p4"> 施工进度： {{save.projectMsg.progressconstruction || '未填写'}}</p>
                      </div>
                      <div class="tt"  v-show="!rrStatus && rStatus">
                        <i class="el-icon-warning" style="font-size: 55px;color: #409EFF"></i>
                        <p class="tips">此站点没有相关信息</p>
                      </div>
                    </div>
                  </el-col>
                </el-row>
                <div class="tt"  v-show="!rrStatus && !rrStatus">
                  <i class="el-icon-warning" style="font-size: 55px;color: #409EFF"></i>
                  <p class="tips">此站点没有相关信息</p>
                </div>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import api from "@/api/index.js";
  export default {
    name: 'home',
    components: {

    },
    data () {
      return {
        page: '0',
        rStatus: false,
        rrStatus: false,
        line: [],
        site: [],
        save: {
          line: {},
          site: {},
          projectMsg: {},
          weather: {},
          userInfo: {}
        },
        tableData: [],
        siteID: '',
        lineID: ''
      }
    },
    methods: {
      // 改变线路
      changeLine (choose) {
        console.log('改变线路', choose)
        var _this = this;
        this.save.line = choose;
        this.lineID = choose.id;
      },
      // 获取线路
      getSelectAll() {
        api.getSelectAll().then(res => {
          console.log('获取线路', res.data.list)
          this.line = res.data.list;
          this.save.line = res.data.list[0];
          this.lineID = res.data.list[0].id;
        })
      },
      // 改变站点
      changeSite(choose) {
        console.log('改变站点', choose);
        this.save.site = choose;
        this.siteID = choose.id;
        this.getMainpageinformactionSelectbyrid(this.save.site.id);
      },
      // 获取站点
      getLinesite(routeid) {
        api.getLinesite(routeid).then(res => {
          console.log('获取站点', res.data.list)
          this.site = res.data.list;
          this.save.site = res.data.list[0];
          this.siteID = res.data.list[0].id;
          this.getMainpageinformactionSelectbyrid(this.save.site.id)
        })
      },
      // 检测项目 列表
      getMeasurementproject(id, pageNum) {
        api.getMeasurementproject(id, pageNum, 10).then(res => {
          console.log('检测项目 列表', res.data.list)
          this.tableData = res.data.list;
        })
      },
      // 获取首页项目信息
      getMainpageinformactionSelectbyrid(rid) {
        api.getMainpageinformactionSelectbyrid(rid).then(res => {
          console.log('获取首页项目信息', res.data.msg)
          if(res.data.msg) {
            this.rrStatus = true;
            this.save.projectMsg = res.data.msg;
            if(res.data.msg.weatherid) {
              this.getWeather(this.save.projectMsg.weatherid);
              this.rStatus = true;
            } else {
              this.rStatus = false;
            }
          } else {
            this.rrStatus = false;
          }
        })
      },
      // 获取天气
      getWeather(id) {
        api.getWeather(id).then(res => {
          console.log('获取天气', JSON.parse(res.data.result))
          this.save.weather = JSON.parse(res.data.result).weatherinfo;
        })
      }
    },
    watch: {
      lineID(val) {
        console.log('lineID', val)
        this.getLinesite(val);
      },
      siteID(val) {
          console.log('siteID', val)
          this.getMeasurementproject(val, 0);
      }
    },
    created() {
      let userInfo = JSON.parse(localStorage.getItem('USERINFO'))
      let _this = this;
      this.getSelectAll();
      console.log('USERINFO', userInfo);
      setTimeout(() => {
        this.line.forEach(item => {
          if(item.id == userInfo.line) {
            this.save.line = item;
          }
        })
        this.site.forEach(item => {
          if(item.id == userInfo.site) {
            this.save.site = item;
          }
        })
        this.save.userInfo = userInfo;
        this.lineID = userInfo.line;
        this.siteID = userInfo.site; 
      }, 200)
    },
  }
</script>
<style lang="stylus" scoped>
.Header
  color #fff
  .select-item
    display flex
    align-items center
    color #fff
    .select_
      color #fff
      border 1px solid #fff
      padding 4px 10px
      border-radius 4px
      cursor pointer
.tableCon
  background #ffffff
  padding 20px
  border-radius 12px
  margin-top 20px
  .title
    margin-bottom 12px
  .fy 
    display flex
    margin-top 20px
    justify-content center
.wratter
  padding 20px
  .con
    background #fff
    padding 20px
    border-radius 12px
    .tt
      display flex
      justify-content center
      align-items center
      flex-direction column
    .text-con
      p
        position relative
        &::before
          display: block
          content: ''
          width: 12px
          height: 12px
          position: absolute
          left: -18px
          top: 6px
          border-radius: 50%
      .p1::before
          background #96e6a1
      .p2::before
          background #8fd3f4
      .p3::before
          background #c2e9fb
      .p4::before
          background #38f9d7



</style>



